.UserPanel body {
    margin: 0;
    padding-top: 40px;
    color: #2e323c;
    background: #f5f6fa;
    position: relative;
    height: 100%;
}
.UserPanel
{
    min-height: 70vh;
    margin-top: 10%;
}
.UserPanel .account-settings .user-profile {
    margin: 0 0 1rem 0;
    padding-bottom: 1rem;
    text-align: center;
}
.UserPanel .account-settings .user-profile .user-avatar {
    margin: 0 0 1rem 0;
}
.UserPanel .account-settings .user-profile .user-avatar img {
    width: 90px;
    height: 90px;
    -webkit-border-radius: 100px;
    -moz-border-radius: 100px;
    border-radius: 100px;
}
.UserPanel .account-settings .user-profile h5.user-name {
    margin: 0 0 0.5rem 0;
}
.UserPanel .account-settings .user-profile h6.user-email {
    margin: 0;
    font-size: 0.8rem;
    font-weight: 400;
    color: #9fa8b9;
}
.UserPanel .account-settings .about {
    margin: 2rem 0 0 0;
    text-align: center;
}
.UserPanel .account-settings .about h5 {
    margin: 0 0 15px 0;
    color: #007ae1;
}
.UserPanel .account-settings .about p {
    font-size: 0.825rem;
}
.UserPanel .form-control {
    border: 1px solid #cfd1d8;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    font-size: .825rem;
    background: #ffffff;
    color: #2e323c;
}

.UserPanel .card {
    background: #ffffff;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border: 0;
    margin-bottom: 1rem;
}